// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// import moment from 'moment';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_MANAGEMENT_REPORT,
  MAINTAINTASK_STATUS
} from '@/utils/constantsFile';

import {
  deviceMaintainReportGetMaintainTaskReport
} from '@/api/report';

import { searchCondetionFunc } from '@/utils/publicFunction';

// import { exportMaintenanceStatisticalNew } from '@/api/export';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  data() {
    return {
      EQUIPMENT_MANAGEMENT_REPORT: EQUIPMENT_MANAGEMENT_REPORT,
      BTN_TEXT: BTN_TEXT,
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      deviceName: '',
      groupOptions: [],
      lineOptions: [],
      lineId: '',
      groupId: '',
      optionsState: [],
      userOptions: [],
      searchData: [],
      userId: '',
      state: '',
      dateTime: '',
      year: '',
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: true,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      tableColumns_new: [],
      // table表头配置
      tableColumns: [
        {
          field: 'factory',
          title: '工厂',
          visible: true,
          width: '120',
          align: 'center'
        },
        {
          field: 'workshop',
          title: EQUIPMENT_MANAGEMENT_REPORT.WORKSHOP_NAME,
          visible: true,
          width: '120',
          align: 'center'
        },
        {
          field: 'productionLine',
          title: '产线',
          visible: true,
          width: '120',
          align: 'center'
        },
        {
          field: 'deviceName',
          title: EQUIPMENT_MANAGEMENT_REPORT.DEVICE_NAME,
          visible: true,
          width: '120',
          align: 'center'
        },
        {
          field: 'devicePriCode',
          title: EQUIPMENT_MANAGEMENT_REPORT.EQUIPMENT_NO,
          visible: true,
          width: '120',
          align: 'center'
        },
        {
          field: 'typeName',
          title: EQUIPMENT_MANAGEMENT_REPORT.DEVICE_TYPE,
          visible: true,
          width: '120',
          align: 'center'
        }
      ],

      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      pickerOptions: {
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
      // 保存自定义之后的列配置
      savedCustomColumns: [],
      // 是否显示自定义列对话框
      isSettingCustomDialogShow: false,
      xModel: 0,
      // xName: '报警代码',
      xOptions: [
        { name: '设备维度', value: 'DEVICE' },
        { name: '周期维度', value: 'CYCLE' }
      ],
      defaultOption: 0,
      savedSearchCondition: []
    };
  },
  watch: {
    groupId(val) {
      this.getLine();
      this.lineId = '';
    }
  },
  computed: {
  },
  created() {
    const start = new Date();

    this.year = this.$utils.toDateString(start, 'yyyy');

    this.findList();
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList(param) {
      this.defaultOption = this.xModel;
      var that = this;
      this.tableColumns_new = [];
      this.tableColumns_new = [...this.tableColumns];

      if (this.xOptions[this.xModel].value === 'CYCLE') {
        this.tableColumns_new.push({
          field: 'y',
          title: '保养周期',
          visible: true,
          width: '160',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return this.showCycleData(row);
            }
          },
          formatter({ cellValue, row }) {
            return that.showCycleDataText(row);
          }
        });
      }

      for (var i = 0; i < 12; i++) {
        ((i) => this.tableColumns_new.push({
          field: (i + 1),
          title: (i + 1) + '月',
          visible: true,
          width: '160',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return this.showMonthData(row, i + 1);
            }
          },
          formatter({ cellValue, row, column }) {
            return that.showMonthDataText(row, i + 1);
          }
        }))(i);
      }

      var conditionJson = {
        conditionJson1: [],
        operatorGroup: 1,
        conditionJson2: [],
        current: this.tablePages.currentPage,
        size: this.tablePages.pageSize
      };
      if (this.savedSearchCondition.length > 0) {
        conditionJson = searchCondetionFunc(...this.savedSearchCondition);
      }

      try {
        this.tableLoadings = true;
        const res = await deviceMaintainReportGetMaintainTaskReport({
          startTime: this.year + '-01-01 14:34:00',
          endTime: this.year + '-12-30 14:34:00',
          deviceMaintainStatisticalAbscissaEnum: this.xOptions[this.xModel].value
        }, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(res);
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // // 加载动画消失
          this.tableLoadings = false;

          if (param === 'export') {
            that.$refs.xGridM.exportDatax('保养统计报表', res.rows);
          }
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    showCycleData(row) {
      var html = [];
      var arrLength = 0;
      for (var keyItem in row.cycleTimeMaintainTaskVo) {
        var item = row.cycleTimeMaintainTaskVo[keyItem];
        for (var key in item) {
          var arr = item[key];
          if (arr.length > arrLength) {
            arrLength = arr.length;
          }
        }
        // var style = 'height:' + (arrLength * 40) + 'px;line-height: ' + (arrLength * 40) + 'px';
        html.push(<div >{keyItem}</div>);
      }
      return html;
    },
    showCycleDataText(row) {
      var html = [];
      var arrLength = 0;
      for (var keyItem in row.cycleTimeMaintainTaskVo) {
        var item = row.cycleTimeMaintainTaskVo[keyItem];
        for (var key in item) {
          var arr = item[key];
          if (arr.length > arrLength) {
            arrLength = arr.length;
          }
        }
        html.push(keyItem);
      }
      return html.join('\n');
    },
    showMonthData(row, month) {
      if (this.xOptions[this.defaultOption].value === 'DEVICE') {
        var html = [];
        for (var key in row.timeMaintainTaskVo) {
          var num = Number(key.substring(5, 7));
          if (num === month) {
            var arr = row.timeMaintainTaskVo[key];
            for (var i = 0; i < arr.length; i++) {
              var name = '';
              if (arr[i].maintainUserNameList) {
                name = '-' + arr[i].maintainUserNameList;
              }

              var pClass = 'pTextClass' + arr[i].maintainState;

              html.push(<p class={pClass} >{'[' + arr[i].cycleName + '] ' + arr[i].maintainTime.substring(0, 10) + name}</p>);
            }
          }
        }
        return html;
      } else if (this.xOptions[this.defaultOption].value === 'CYCLE') {
        var html = [];
        for (var keyItem in row.cycleTimeMaintainTaskVo) {
          var item = row.cycleTimeMaintainTaskVo[keyItem];
          for (var key in item) {
            var num = Number(key.substring(5, 7));
            if (num === month) {
              var arr = item[key];
              for (var i = 0; i < arr.length; i++) {
                var name = '';
                if (arr[i].maintainUserNameList) {
                  name = '-' + arr[i].maintainUserNameList;
                }
                var pClass = 'pTextClass' + arr[i].maintainState;
                html.push(<p class={pClass} >{'[' + arr[i].cycleName + '] ' + arr[i].maintainTime.substring(0, 10) + name}</p>);
              }
            }
          }
        }
        return html;
      }
    },
    showMonthDataText(row, month) {
      if (this.xOptions[this.xModel].value === 'DEVICE') {
        var html = [];
        for (var key in row.timeMaintainTaskVo) {
          var num = Number(key.substring(5, 7));
          if (num === month) {
            var arr = row.timeMaintainTaskVo[key];
            for (var i = 0; i < arr.length; i++) {
              var name = '';
              if (arr[i].maintainUserNameList) {
                name = '-' + arr[i].maintainUserNameList;
              }
              html.push('[' + arr[i].cycleName + '] ' + arr[i].maintainTime.substring(0, 10) + name);
            }
          }
        }
        return html.join('\n');
      } else if (this.xOptions[this.xModel].value === 'CYCLE') {
        var html = [];
        for (var keyItem in row.cycleTimeMaintainTaskVo) {
          var item = row.cycleTimeMaintainTaskVo[keyItem];
          for (var key in item) {
            var num = Number(key.substring(5, 7));
            if (num === month) {
              var arr = item[key];
              for (var i = 0; i < arr.length; i++) {
                var name = '';
                if (arr[i].maintainUserNameList) {
                  name = '-' + arr[i].maintainUserNameList;
                }
                html.push('[' + arr[i].cycleName + '] ' + arr[i].maintainTime.substring(0, 10) + name);
              }
            }
          }
        }
        return html.join('\n');
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePages.currentPage = 1;
      }
      this.findList();
    },
    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search() {
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },
    /**
     * @description: 重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.deviceName = '';
      this.userId = '';
      this.deviceName = '';
      this.state = '';
      this.typeValue = '';
      this.groupValue = '';
      this.groupId = '';
      this.lineId = '';
      this.getLine();
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
      this.dateTime = [
        this.$utils.toDateString(start, 'yyyy-MM-dd'),
        this.$utils.toDateString(end, 'yyyy-MM-dd')
      ];
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      this.findList('export');
    }
  }
};
