// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
import moment from 'moment';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_MANAGEMENT_REPORT,
  FIXTASK_STATUS
} from '@/utils/constantsFile';
// 导入所需接口
import { GetRepairStatisticalDetail } from '@/api/equipmentmanagement';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  props: {
    deviceCode: String
  },
  data() {
    return {
      EQUIPMENT_MANAGEMENT_REPORT: EQUIPMENT_MANAGEMENT_REPORT,
      BTN_TEXT: BTN_TEXT,
      FIXTASK_STATUS: FIXTASK_STATUS,
      dateTime: '',
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: true,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'occurTime',
          title: EQUIPMENT_MANAGEMENT_REPORT.REPAIR_APPLICATION_TIME,
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'endTime',
          title: EQUIPMENT_MANAGEMENT_REPORT.ENDTIME,
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'repairTypeNames',
          title: EQUIPMENT_MANAGEMENT_REPORT.FAULT_TYPE,
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'repairUserName',
          title: EQUIPMENT_MANAGEMENT_REPORT.MAINTAINER,
          width: '',
          align: 'center'
        },
        {
          field: 'occurUserName',
          title: EQUIPMENT_MANAGEMENT_REPORT.REPAIRER,
          width: '',
          align: 'center'
        },
        {
          field: 'repairState',
          title: EQUIPMENT_MANAGEMENT_REPORT.STATE,
          width: '',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return FIXTASK_STATUS[row.repairState];
            }
          }
        }
      ],

      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      pickerOptions: {
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      }
    };
  },
  watch: {},
  created() {
    const end = new Date();
    const start = new Date();

    start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
    this.dateTime = [
      this.$utils.toDateString(start, 'yyyy-MM-dd'),
      this.$utils.toDateString(end, 'yyyy-MM-dd')
    ];
    // 查询table
    this.findList();
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      try {
        this.tableLoadings = true;
        const res = await GetRepairStatisticalDetail(
          {
            size: this.tablePages.pageSize,
            current: this.tablePages.currentPage,
            startTime: this.dateTime[0] + ' 00:00:00',
            endTime: this.dateTime[1] + ' 23:59:59',
            deviceCode: this.deviceCode
          },
          {}
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search() {
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      try {
        if (this.typeValue !== '') {
          var typeId = this.typeValue[this.typeValue.length - 1];
        }
        this.tableLoadings = true;
        const params = {
          size: 100000,
          current: 1,
          startTime: this.dateTime[0] + ' 00:00:00',
          endTime: this.dateTime[1] + ' 23:59:59',
          state: this.state,
          typeId: typeId,
          userId: this.userId,
          workshopId: this.groupValue,
          deviceName: this.deviceName
        };
        const res = await GetRepairStatisticalDetail(params);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableLoadings = false;
          this.outData = res.rows;
          this.outData.forEach(function(item) {
            var t1 = moment(item.startTime);
            var t2 = moment(item.endTime);
            item.totalTime = t2.diff(t1, 'seconds');
          });
          this.$refs.xGrid.exportData('基础信息', this.outData);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};
